.Nav {
  width: 60%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Nav .tab {
  font-size: 4rem;
  text-decoration: none;
  color: black;
  padding: 0.5rem 3rem;
  border-bottom: 0.5rem solid transparent;
  white-space: nowrap;
}

.Nav .tab:hover {
  background-color: rgba(0, 0, 50, 0.5);
  color: white;
  border-radius: 0 0 0.5rem 0.5rem;
  border-bottom: 0.5rem solid rgba(0, 0, 50, 0.4);
}

.Nav .tab.tab-active {
  background-color: rgba(0, 0, 50, 0.5);
  color: white;
  border-radius: 0 0 0.5rem 0.5rem;
  border-bottom: 0.5rem solid rgba(0, 0, 50, 0.8);
  transition: border-bottom 1s ease-in-out;
}
